import React from "react";
import Common from "./Common.jsx";
import web from "./images/imagess.jpg";

const Home = () => {
  return (
    <>
      <Common
        name="Welcome to the world of "
        tagline="Explore the world like never before"
        imgsrc={web}
        visit="/services"
        btname="Get Started"
      />
    </>
  );
};

export default Home;
