import React from "react";
import Common from "./Common.jsx";
import web from "./images/imagess.jpg";

const About = () => {
  return (
    <>
      <Common
        name="Know all about "
        tagline="Your one stop destination to traveling"
        imgsrc={web}
        visit="/contact"
        btname="Contact Now"
      />
    </>
  );
};

export default About;
